import React from 'react';
import {Grid, Row, Col} from 'react-bootstrap';

const HomeBand = ({titlePart1, titlePart2, caption}) => (
    <section className="band band-home margin-bottom-l">
        <Grid>
            <Row className="margin-bottom-s margin-top-s">
                <Col sm={6}>
                    <h4 className="title">{titlePart1} <br className="hidden-xs"/>{titlePart2}</h4>
                </Col>
                <Col sm={6}>
                    <h4 className="caption margin-top-s">{caption}</h4>
                </Col>
            </Row>
        </Grid>
    </section>
);

export default HomeBand;



