import React, {Component} from "react";
import {Link} from "react-router-dom";
import {Button, Col, Grid, Row, Image} from "react-bootstrap";
import FormContainer from "./FormContainer";
import EmailForm from "./EmailForm";
import Card from "./Card";
import HomeBand from "./HomeBand";

import brands from "../img/brands.jpg";
import find from "../img/find.jpg";
import treat from "../img/treat.jpg";
import passion from "../img/passion.jpg";
import home from "../img/email-home.jpg";

class Home extends Component {
    componentDidMount() {
        window.scrollTo(0, 0);
    }

    componentDidUpdate() {
        window.scrollTo(0, 0);
    }

    render() {
        return (
            <div>
                <section className="splash-container margin-bottom-l home">
                    <Grid className="splash">
                        <div className="hero">
                            <h1 className="heading">
                                Fitness apparel<br/>at your fingertips
                            </h1>
                            <h4 className="subheading">
                                Sign up to get started with the<br/>Dot Confirmed Shop Builder<br/>to win a $100 lululemon gift card!
                            </h4>
                            <Button bsStyle="primary" onClick={() => this.form.scrollIntoView()}>
                                Sign up now
                            </Button>
                        </div>
                    </Grid>
                </section>

                <section className="margin-bottom-l">
                    <Grid>
                        <Row className="margin-bottom-l">
                            <Col sm={12} style={{textAlign: "center"}}>
                                <h3 className="mt-0">Preloved activewear from your favorite brands all in one place</h3>
                            </Col>
                        </Row>
                        <Row className="margin-bottom-m">
                            <Col sm={12}>
                                <Image src={brands} responsive/>
                            </Col>
                        </Row>
                    </Grid>
                </section>

                <HomeBand
                    titlePart1="Athleisure"
                    titlePart2=""
                    caption='"Apparel for everything from yoga, to a wine Wednesday patio with friends"'
                    />

                <section>
                    <Grid>
                        <Row className="margin-bottom-l">
                            <Col sm={8}>
                                <h2 className="mt-0 mb-0">Dot Confirmed lets you</h2>
                            </Col>
                            <Col sm={4}>
                                <div style={{marginTop: "25px"}}>
                                    <Link to="/more" className="h5">Learn More</Link>
                                </div>
                            </Col>
                        </Row>

                        <Row>
                            <Col sm={4}>
                                <Card image={find} caption="Find your styles">
                                    <p>
                                        It’s hard balancing life and fitness! We pull the pieces you want to see, so you
                                        can reach your goals wearing the perfect activewear.
                                    </p>
                                </Card>
                            </Col>
                            <Col sm={4}>
                                <Card image={treat} caption="Buy preloved activewear">
                                    <p>
                                        A refreshing new way to buy the activewear that makes you look and feel confident.
                                        It’s never been easier to get the limited edition styles you missed out on.
                                    </p>
                                </Card>
                            </Col>
                            <Col sm={4}>
                                <Card image={passion} caption="Sell your activewear">
                                    <p>
                                        Sell your activewear to make room for more! Hosting an online shop lets you refresh
                                        your look and fund your passion. The possibilities are endless.
                                    </p>
                                </Card>
                            </Col>
                        </Row>
                    </Grid>
                </section>

                <section className="margin-bottom-l" ref={form => this.form = form}>
                    <Grid>
                        <FormContainer src={home}>
                            <Row>
                                <Col smOffset={6} sm={5}>
                                    <EmailForm
                                        legend="Win a $100 lululemon gift card!"
                                        help="Find Dot Confirmed on the App Store this September! In the meantime, get the Shop Builder and start listing your activewear for a chance to win."
                                    />
                                </Col>
                            </Row>
                        </FormContainer>
                    </Grid>
                </section>
            </div>
        );
    }
}

export default Home;



