import React from 'react';
import {Grid, Row, Col} from 'react-bootstrap';

const Band = ({titlePart1, titlePart2, caption}) => (
    <section className="band band-about margin-bottom-l">
        <Grid>
            <Row className="margin-bottom-s margin-top-s">
                <Col sm={6}>
                    <h4 className="title">{titlePart1} <br className="hidden-xs"/>{titlePart2}</h4>
                </Col>
                <Col sm={6}>
                    <h1 className="caption">{caption}</h1>
                </Col>
            </Row>
        </Grid>
    </section>
);

export default Band;


