import React from 'react';
import {Image} from 'react-bootstrap';

const Card = ({image, caption, children}) => (
    <div className="card">
        <Image src={image} responsive/>
        {caption && <h4>{caption}</h4>}
        {children}
    </div>
);

export default Card;


