import React, {Component} from "react";
import {Image, Nav, Navbar, NavItem} from "react-bootstrap";
import {LinkContainer} from "react-router-bootstrap";
import {Link} from "react-router-dom";
import Icon from "react-fontawesome";

import logo from "../img/love_logo-dark.svg";

const DEVICE_SIZE = 768; // Small devices (tablets, 768px and up)
const SCROLL_TOLERANCE = 20;
const FACEBOOK = 'https://www.facebook.com/dotconfirmed/';
const INSTAGRAM = 'https://www.instagram.com/dotconfirmed/';

const openTab = (url) => window.open(url, '_blank');
const NavHeader = ({className, children}) => <li className={['nav-heading', className].join(' ')}>{children}</li>;

class MyNavbar extends Component {
    constructor(props) {
        super(props);

        this.state = {
            isScrolling: false,
            isHovering: false
        };

        this.handleScroll = this.handleScroll.bind(this);
        this.handleMouseOver = this.handleMouseOver.bind(this);
        this.handleMouseOut = this.handleMouseOut.bind(this);
    }

    handleScroll() {
        let isScrolling = document.body.scrollTop > SCROLL_TOLERANCE;

        if (isScrolling !== this.state.isScrolling) {
            this.setState({isScrolling});
        }
    }

    handleMouseOver() {
        if (!this.state.isHovering) {
            this.setState({isHovering: true});
        }
    }

    handleMouseOut() {
        if (this.state.isHovering) {
            this.setState({isHovering: false});
        }
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    render() {
        let {isScrolling, isHovering} = this.state;
        let inverse = !(isScrolling || isHovering) && (window.innerWidth > DEVICE_SIZE);

        return (
            <Navbar fixedTop
                    collapseOnSelect
                    inverse={inverse}
                    onMouseOver={this.handleMouseOver}
                    onMouseOut={this.handleMouseOut}>
                <Navbar.Header>
                    <Navbar.Brand>
                        <Link to='/'><Image src={logo} rounded/> Dot Confirmed</Link>
                    </Navbar.Brand>
                    <Navbar.Toggle />
                </Navbar.Header>
                <Navbar.Collapse>
                    <Nav pullLeft>
                        <NavHeader className='visible-xs'>Getting Started</NavHeader>

                        <LinkContainer to='/more'><NavItem>Learn More</NavItem></LinkContainer>
                        <LinkContainer to='/about'><NavItem>About Us</NavItem></LinkContainer>
                    </Nav>
                    <Nav pullRight>
                        <NavHeader className='visible-xs'>Follow Us</NavHeader>

                        <NavItem onSelect={() => openTab(FACEBOOK)}>
                            <span className='visible-xs'>Facebook</span>
                            <Icon className='hidden-xs' name='facebook-square' size='lg'
                                  style={{marginRight: '-15px'}}/>
                        </NavItem>

                        <NavItem onSelect={() => openTab(INSTAGRAM)}>
                            <span className='visible-xs'>Instagram</span>
                            <Icon className='hidden-xs' name='instagram' size='lg'/>
                        </NavItem>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        );
    }
}

export default MyNavbar;



