import React, {Component} from "react";
import {Col, Grid, Image, Row} from "react-bootstrap";
import FormContainer from "./FormContainer";
import EmailForm from "./EmailForm";
import Card from "./Card";

import search from "../img/search.jpg";
import save from "../img/save.jpg";
import sell from "../img/sell.jpg";
import trust from "../img/trust.jpg";
import connect from "../img/connect.jpg";
import buy from "../img/buy.jpg";
import split from "../img/more-split.jpg";
import more from "../img/email-more.jpg";

class More extends Component {
    componentDidMount() {
        window.scrollTo(0, 0);
    }

    componentDidUpdate() {
        window.scrollTo(0, 0);
    }

    render() {
        return (
            <div>
                <section className="splash-container margin-bottom-l more">
                    <Grid className="splash">
                        <div className="sub-hero">
                            <h1 className="heading">How the app works</h1>
                        </div>
                    </Grid>
                </section>

                <section>
                    <Grid>
                        <Row>
                            <Col sm={6}>
                                <h2 className="mt-0">Search</h2>
                            </Col>
                            <Col sm={6}>
                                <Card image={search}>
                                    <p>
                                        We make it easy for you to find the perfect activewear. Browse to your heart’s
                                        content, or customize your preferences so we know exactly what you’re looking
                                        for. We pick items that match your size and style - it’s like having your own
                                        personal shopper!
                                    </p>
                                </Card>
                            </Col>
                        </Row>

                        <Row>
                            <Col sm={6}>
                                <h2 className="mt-0">Save</h2>
                            </Col>
                            <Col sm={6}>
                                <Card image={save}>
                                    <p>
                                        Found something you love? Store all your finds until you’re ready to buy. Saving
                                        items for later lets you mix and match to design a whole new look that you can
                                        return to anytime. Once you’re ready, letting the seller know you’re interested
                                        is only a tap away!
                                    </p>
                                </Card>
                            </Col>
                        </Row>

                        <Row>
                            <Col sm={6}>
                                <h2 className="mt-0">Buy</h2>
                            </Col>
                            <Col sm={6}>
                                <Card image={buy}>
                                    <p>
                                        Never worry about missing out on a size or style again! Discovering the right
                                        outfit is easy, and giving it a new home is only a tap away. Let shops know you’re
                                        interested, and arrange a time and place to pick up the newest addition to your
                                        athleisure collection.
                                    </p>
                                </Card>
                            </Col>
                        </Row>
                    </Grid>
                </section>

                <section className="margin-bottom-l">
                    <Image src={split} responsive/>
                </section>

                <section>
                    <Grid>
                        <Row>
                            <Col sm={6}>
                                <h2 className="mt-0">Sell</h2>
                            </Col>
                            <Col sm={6}>
                                <Card image={sell}>
                                    <p>
                                        Hosting a shop is simple. Whether you’re saving for your dream trip, or the gear
                                        you’ve had your eye on, our members use their extra income to fuel their
                                        passions. Reviews and chat help you get to know shoppers so you can choose what
                                        works best for you.
                                    </p>
                                </Card>
                            </Col>
                        </Row>

                        <Row>
                            <Col sm={6}>
                                <h2 className="mt-0">Trust</h2>
                            </Col>
                            <Col sm={6}>
                                <Card image={trust}>
                                    <p>
                                        It’s easy for you to feel confident with your shopping decisions. Our community
                                        relies on honest reviews, so you can build trust with other members. We also
                                        do our very best to ensure all activewear is Dot Confirmed; verified by brand, price tag, or rip tag!
                                    </p>
                                </Card>
                            </Col>
                        </Row>

                        <Row>
                            <Col sm={6}>
                                <h2 className="mt-0">Connect</h2>
                            </Col>
                            <Col sm={6}>
                                <Card image={connect}>
                                    <p>
                                        Follow the shops that fit your style and be the first to know when they post new
                                        activewear! Share your finds with friends and maintain a high rating to boost your
                                        status and expand your circle.
                                    </p>
                                </Card>
                            </Col>
                        </Row>
                    </Grid>
                </section>

                <section className="margin-bottom-l">
                    <Grid>
                        <FormContainer src={more}>
                            <Row>
                                <Col smOffset={6} sm={5}>
                                    <EmailForm
                                        legend="Win a $100 lululemon gift card!"
                                        help="Find Dot Confirmed on the App Store this September! In the meantime, get the Shop Builder and start listing your activewear for a chance to win."
                                    />
                                </Col>
                            </Row>
                        </FormContainer>
                    </Grid>
                </section>
            </div>
        );
    }
}

export default More;



