import React, {Component} from "react";
import {Link} from "react-router-dom";
import {Col, Grid, Row} from "react-bootstrap";

class Privacy extends Component {
    componentDidMount() {
        window.scrollTo(0, 0);
    }

    componentDidUpdate() {
        window.scrollTo(0, 0);
    }

    render() {
        return (
            <div>
                <section className="splash-container margin-bottom-l privacy">
                    <Grid className="splash">
                        <div className="sub-hero">
                            <h1 className="heading">Privacy Statement</h1>
                        </div>
                    </Grid>
                </section>

                <section>
                    <Grid>
                        <Row className="margin-bottom-l">
                            <Col md={5}>
                                <h2 className="mt-0">1. Overview</h2>
                            </Col>
                            <Col md={1}></Col>
                            <Col md={6}>
                                <p>
                                    This Privacy Statement explains how we collect and use your personal information on
                                    this website (the <b>“Site”</b>) and our software application (the <b>“App”</b>). References in
                                    Privacy Statement to our Site includes <Link to="/#">www.dotconfirmed.com</Link> and all associated web pages,
                                    as well as the App. References in these Terms of Use to the App includes all iterations of the
                                    Dot Confirmed App run on any platform (including web, Android and Mac iOS), as
                                    well as updates thereto and any other software available for download from our Site.
                                    <b> <u>You should read this Privacy Statement carefully before using our Site.</u></b> If you do
                                    not accept any part of this Privacy Statement, you should cease using our Site
                                    immediately. If you have any questions about this website, please contact <a href="mailto:admin@dotconfirmed.com">admin@dotconfirmed.com.</a>
                                </p>
                                <p>
                                    The terms <b>“we”</b>, <b>“us” “our”</b>, <b>“you”</b>, <b>“your”</b>, <b>“Content”</b>, <b>“Site”</b>, <b>“Account”</b> and <b>“App” </b>
                                    have the same meanings as in the <Link to="/terms">Terms of Use</Link> of our Site, as amended from time to
                                    time.
                                </p>
                                <p>
                                    Scott Campbell and Kristen Brietzke are jointly Accountable for our personal information handling practices.
                                </p>
                                <p>
                                    This privacy statement is effective as of Saturday, July 22, 2017.
                                </p>
                            </Col>
                        </Row>
                        <Row className="margin-bottom-l">
                            <Col md={5}>
                                <h2 className="mt-0">2. What Personal Information Do We Collect?</h2>
                            </Col>
                            <Col md={1}></Col>
                            <Col md={6}>
                                <p>
                                    Personal information is information that identifies you or could be combined by us
                                    or our service providers and affiliates with other information to identify you. This
                                    information includes your personal e-mail address, your home address, your telephone
                                    number(s), your image, and may include your age, your income and other similar
                                    information when associated with you. Personal information may also be information
                                    containing details as to whether you have opened our promotional e-mails or how you
                                    have used our website, if we can associate that personal information with you. If
                                    you are a business customer or prospective business customer, personal information
                                    does not include your title, business address and business telephone number when we
                                    use that information to contact you in your business capacity.
                                </p>
                                <p>
                                    We collect personal information from you when you create an Account, make a purchase,
                                    enter a contest or promotion, communicate with us using our web forms to make a
                                    request or ask a question, submit a product review, take a survey or otherwise
                                    submit personal information to us with your communication. For example, we will
                                    request credit card and other identifying information to process a credit card
                                    transaction when you purchase a service through the Site.
                                </p>
                                <p>
                                    We may receive personal information about you from other sources. For example, we
                                    will receive information from credit card processors regarding whether the credit
                                    card details you gave us have been accepted or declined. We may also acquire
                                    information about you from other promotional or marketing companies with whom
                                    you shared your information and whom you have allowed to provide information to us.
                                </p>
                                <p>
                                    This privacy statement is effective as of Saturday, July 22, 2017.
                                </p>
                            </Col>
                        </Row>
                        <Row className="margin-bottom-l">
                            <Col md={5}>
                                <h2 className="mt-0">3. How Do We Use Your Personal Information?</h2>
                            </Col>
                            <Col md={1}></Col>
                            <Col md={6}>
                                <p>
                                    We use your information to provide services and to manage our business operations.
                                    Some examples include:
                                </p>
                                <ol type="a">
                                    <li>to register your Account;</li>
                                    <li>to authenticate you when you log into your Account;</li>
                                    <li>to fulfill your requests for products and services;</li>
                                    <li>to communicate with you about your requests;</li>
                                    <li>to administer contests and promotions that you participate in;</li>
                                    <li>to recommend or to offer you other products and services based on your inferred
                                        interests;</li>
                                    <li>to post your product reviews;</li>
                                    <li>to communicate with you regarding customer service issues and any product recalls
                                        or warranty service; and</li>
                                    <li>to protect the security or integrity of our websites and our business.</li>
                                </ol>
                                <p>
                                    We retain your information for as long as necessary to fulfill the purposes for which
                                    we collected the information or as required by law. We maintain a records retention
                                    and destruction program to destroy information when it is no longer needed or required.
                                </p>
                            </Col>
                        </Row>
                        <Row className="margin-bottom-l">
                            <Col md={5}>
                                <h2 className="mt-0">4. Personalized Offers and Web Analytics</h2>
                            </Col>
                            <Col md={1}></Col>
                            <Col md={6}>
                                <p>
                                    We may automatically collect information when you visit our website in order to
                                    customize your experience. For example, we may collect information about what site
                                    referred you to our website, your browser type, operating system type and version,
                                    language, and your Internet Protocol (“IP”) address. We may also collect information
                                    about how you browse through our website, including logging the pages you visit and
                                    the amount of time you spend on certain pages. We may use this information for a
                                    number of technical purposes to ensure that our pages load correctly in your browser,
                                    as well as to personalize our offers to you. We may also use this information to
                                    improve our website. For example:
                                </p>
                                <ol type="a">
                                    <li>we may use IP addresses to correct pages for your geographical location;</li>
                                    <li>we may log the frequency of visits to certain pages on our website and how
                                        visitors navigate through our website in order to detect problems, improve our
                                        website and learn about what products and services are of interest to our customers;</li>
                                    <li>we may use information about products that you have clicked on to make suggestions
                                        about other products or services that you might be interested in viewing; and</li>
                                    <li>we may track whether certain types of promotional e-mails were opened by you and
                                        whether you sought information about a particular product or service in order to
                                        make inferences about other products and services you might be interested in.</li>
                                </ol>
                            </Col>
                        </Row>
                        <Row className="margin-bottom-l">
                            <Col md={5}>
                                <h2 className="mt-0">5. Your Ad Choices: Interest-Based Advertising</h2>
                            </Col>
                            <Col md={1}></Col>
                            <Col md={6}>
                                <p>
                                    Our Site may use small pixel files (small images) and cookies (small text files) to
                                    keep track of your browser so that you will be recognized when you visit other
                                    websites where our third-party ad management providers may show you advertisements
                                    from us based on your visit to our website. Some of these advertisements may be
                                    customized to particular products and services that you viewed or they may be based
                                    on inferences about products and services that you might be interested in. This is
                                    called “interest-based advertising”.
                                </p>
                                <p>
                                    These pixel files and cookies are generally valid for a limited number of days
                                    (usually no more than 90 days) and are provided by our ad management providers.
                                    We require our ad management providers to participate in the Digital Advertising
                                    Alliance of Canada’s Ad Choices Program. Visit the <a href="http://www.youradchoices.ca">Digital Advertising Alliance of
                                    Canada: Your AdChoices</a> where you can find out
                                    more information about digital advertising and how you can opt-out of interest-based
                                    advertising. You can also opt-out of interest-based advertising using the link
                                    provided in any of our interest-based advertisements. Look for the Ad Choices icon
                                    or other link provided.
                                </p>
                                <p>
                                    Please note that if you opt-out of interest-based advertising you may still see
                                    advertisements from us on other websites. However, they will not be customized based
                                    on your browsing history on our website. In addition, we will continue to customize
                                    our offers of products and services to you when you are on our website unless you
                                    block cookies from our website. If you block cookies on our website, certain functions,
                                    such as the purchasing services not function properly because our Site must
                                    recognize you as you move through the checkout process.
                                </p>
                            </Col>
                        </Row>
                        <Row className="margin-bottom-l">
                            <Col md={5}>
                                <h2 className="mt-0">6. Regarding Children and Minors</h2>
                            </Col>
                            <Col md={1}></Col>
                            <Col md={6}>
                                <p>
                                    Our website and the services offered on our website are not directed to children
                                    under the age of 16 years. We will not knowingly collect personal information about
                                    a child under the age of 16.
                                </p>
                            </Col>
                        </Row>
                        <Row className="margin-bottom-l">
                            <Col md={5}>
                                <h2 className="mt-0">7. Do We Disclose Your Personal Information to Others?</h2>
                            </Col>
                            <Col md={1}></Col>
                            <Col md={6}>
                                <p>
                                    We may share your personal information with our service providers that help us with
                                    our business operations and with marketing and promotional services. We require our
                                    service providers to keep your personal information secure. In addition, our service
                                    providers may not use or disclose personal information for any purpose other than
                                    providing the services on our behalf unless we or they have obtained your consent.
                                </p>
                                <p>
                                    We may transfer any information we have about you in connection with a merger or sale
                                    (including transfers made as part of insolvency or bankruptcy proceedings) involving
                                    all or part of our business or as part of a corporate reorganization or stock sale
                                    or other change in corporate control.
                                </p>
                                <p>
                                    We may also disclose your personal information if it is necessary for us to collect
                                    a debt from you or where we have reason to believe that it is relevant to the
                                    investigation or decision to investigate a breach of the laws of Canada, a province
                                    or territory of Canada, or a foreign jurisdiction, and we are legally permitted to
                                    do so.
                                </p>
                            </Col>
                        </Row>
                        <Row className="margin-bottom-l">
                            <Col md={5}>
                                <h2 className="mt-0">8. Law Enforcement Requests</h2>
                            </Col>
                            <Col md={1}></Col>
                            <Col md={6}>
                                <p>
                                    We will disclose personal information without your knowledge or consent if we receive
                                    an order, subpoena, warrant or other legal requirement issued by a court, tribunal,
                                    regulator or other person with jurisdiction to compel disclosure of your personal
                                    information. If we receive a request from a police officer or other law enforcement
                                    agency for access to your personal information in the course of an actual or potential
                                    investigation, our policy is to require the police officer or law enforcement agency
                                    to obtain an order, subpoena or warrant, unless the information is urgently required
                                    to prevent serious bodily injury or serious damage to personal property.
                                </p>
                            </Col>
                        </Row>
                        <Row className="margin-bottom-l">
                            <Col md={5}>
                                <h2 className="mt-0">9. Storage Outside of Canada</h2>
                            </Col>
                            <Col md={1}></Col>
                            <Col md={6}>
                                <p>
                                    Your personal information may be used or stored by us or our service providers outside
                                    of Canada meaning that it may also be subject to the laws of the country in which it
                                    is used or stored.
                                </p>
                            </Col>
                        </Row>
                        <Row className="margin-bottom-l">
                            <Col md={5}>
                                <h2 className="mt-0">10. Storage Outside of Canada</h2>
                            </Col>
                            <Col md={1}></Col>
                            <Col md={6}>
                                <p>
                                    If you register for an Account, you will have the option to choose what marketing
                                    and promotional e-mails and communications you receive from us. You can also store
                                    information such as your home address and credit card information in order to make
                                    future purchases faster. You will also be able to review your order history and past
                                    product reviews.
                                </p>
                                <p>
                                    You can modify your information by logging into your Account and making the requested
                                    changes. You cannot modify a product review once it has been submitted. However, you
                                    can delete your product review.
                                </p>
                                <p>
                                    You may also choose “close my Account” and we will remove your Account information
                                    from active use. Any product reviews will be listed as from a “former user”.
                                </p>
                                <p>
                                    If you do not re-activate your Account within 12 months, we will permanently delete
                                    your Account. You may also contact us at <a href="mailto:admin@dotconfirmed.com">admin@dotconfirmed.com</a> to make a deletion
                                    request. Please note that we still maintain information that you provided to us for
                                    audit purposes or to comply with our legal obligations. We may also de-identify your
                                    information in order to continue to use it for research and analytics purposes to
                                    understand our business, to make product improvements and to make available to third
                                    parties. We prohibit our employees and third parties from attempting to re-identify
                                    information that we have de-identified.
                                </p>
                            </Col>
                        </Row>
                        <Row className="margin-bottom-l">
                            <Col md={5}>
                                <h2 className="mt-0">11. How Do You Access and Modify Your Personal Information or Make a Complaint?</h2>
                            </Col>
                            <Col md={1}></Col>
                            <Col md={6}>
                                <p>
                                    If you have an Account, you can log in to access and modify the personal information
                                    stored with your Account. If you have questions or concerns about other personal
                                    information collected by us and would like assistance accessing that information,
                                    please contact us at <a href="mailto:admin@dotconfirmed.com">admin@dotconfirmed.com.</a>
                                </p>
                            </Col>
                        </Row>
                        <Row className="margin-bottom-l">
                            <Col md={5}>
                                <h2 className="mt-0">12. How Do We Protect Your Personal Information?</h2>
                            </Col>
                            <Col md={1}></Col>
                            <Col md={6}>
                                <p>
                                    We take administrative, technical and physical measures to safeguard your personal
                                    information against unauthorized access, unauthorized disclosure, theft and misuse.
                                    This includes limiting access of employees to, and the use of, your personal
                                    information through the use of passwords and graduated levels of clearance. We do
                                    not publish all of our security measures online because this may reduce their
                                    effectiveness. We take physical precautions to ensure that the computer servers on
                                    which your personal information is stored and archived are secure and that access to
                                    such servers is protected. We educate our employees with respect to their obligations
                                    to protect your personal information.
                                </p>
                                <p>
                                    Although we take precautions against possible breaches of our security systems, no
                                    company can fully eliminate the risks of unauthorized access to your personal
                                    information and no website is completely secure. We cannot guarantee that unauthorized
                                    access, hacking, data loss or breaches of our security systems will never occur.
                                    Accordingly, you should not transmit personal information to us using our website if
                                    you consider that information to be sensitive.
                                </p>
                            </Col>
                        </Row>
                        <Row className="margin-bottom-l">
                            <Col md={5}>
                                <h2 className="mt-0">13. Links to Third-Party Websites and Social Network Plug-Ins</h2>
                            </Col>
                            <Col md={1}></Col>
                            <Col md={6}>
                                <p>
                                    You may be able to access third-party websites through links available on our websites.
                                    You may also connect to social networks such as Facebook, Twitter and Instagram.
                                    These links are provided for your convenience. We do not have any control over those
                                    third-party websites and we do not provide any guarantee that the privacy practices
                                    of the hosts of those websites meet our standards. Your use of such third-party websites
                                    is at your own risk and will be governed by the privacy policies of those websites
                                    and not by this Privacy Statement. Do not transmit personal information using those
                                    websites without reading the privacy policies governing those websites.
                                </p>
                                <p>
                                    You may create links between our website and social networking sites. If you do so,
                                    we may be able to publish updates about our products and services to your social
                                    network account through the links that you establish. You may terminate those links
                                    at any time by modifying your social network account.
                                </p>
                                <p>
                                    If you are logged into a social networking site when visiting our page, your visit
                                    to our website may be personalized based on your social networking profile.
                                </p>
                                <p>
                                    You may change the way social networking sites and our sites interact by modifying
                                    your privacy settings in your social networking account. You can also log-out of your
                                    social networking account before visiting our website. For more information, please
                                    refer to your social networking site's privacy policy, which governs your use of these
                                    social plug-in features.
                                </p>
                            </Col>
                        </Row>
                    </Grid>
                </section>
            </div>
        );
    }
}

export default Privacy;



