import React from "react";
import {Image} from "react-bootstrap";

const FormContainer = ({src, children}) => (
    <div className='form-container'>
        <Image src={src} responsive/>
        <div className='form-overlay'>
            {children}
        </div>
    </div>
);

export default FormContainer;



