import React, {Component} from "react";
import {Col, Grid, Row} from "react-bootstrap";
import FormContainer from "./FormContainer";
import CommentForm from "./CommentForm";
import Card from "./Card";
import Band from "./Band";

import scottFunny from "../img/scott-funny.jpg";
import kristenFunny from "../img/kristen-funny.jpg";
import more from "../img/comment.jpg";

class About extends Component {
    componentDidMount() {
        window.scrollTo(0, 0);
    }

    componentDidUpdate() {
        window.scrollTo(0, 0);
    }

    render() {
        return (
            <div>
                <section className="splash-container margin-bottom-l about">
                    <Grid className="splash">
                        <div className="sub-hero">
                            <h1 className="heading">Who we are</h1>
                        </div>
                    </Grid>
                </section>

                <section>
                    <Grid>
                        <Row className="margin-bottom-l">
                            <Col sm={6}>
                                <h2 className="mt-0">The mission</h2>
                            </Col>
                            <Col sm={6}>
                                <p>
                                    Dot Confirmed celebrates self-confidence and authenticity. It’s our mission to build
                                    connections and inspire you to live your best life. We’re creating an innovative
                                    app that can keep up with your lifestyle and help you meet your goals.
                                </p>
                            </Col>
                        </Row>

                        <Row>
                            <Col sm={6}>
                                <Card image={scottFunny}>
                                    <h2 className="mt-0">Scott Campbell</h2>
                                    <h5>Co-Founder</h5>
                                    <p>
                                        Hola! I’m the developer, designer, and all-around creative responsible for
                                        ensuring you enjoy every second of your Dot Confirmed experience. I can’t
                                        imagine a better way to kick off my mornings than with pancakes and a comic.
                                    </p>
                                </Card>
                            </Col>
                            <Col sm={6}>
                                <Card image={kristenFunny}>
                                    <h2 className="mt-0">Kristen Brietzke</h2>
                                    <h5>Co-Founder</h5>
                                    <p>
                                        Hey there! I’m bringing our vision and spirit to life; driving our marketing and
                                        communication strategies towards growing the Dot Confirmed family. I picture
                                        myself travelling the world with a glass of wine in my hand.
                                    </p>
                                </Card>
                            </Col>
                        </Row>
                    </Grid>
                </section>

                <Band
                    titlePart1="We believe in"
                    titlePart2="keeping things"
                    caption="Fresh, Authentic, Inspired, Personal."
                />

                <section className="margin-bottom-l">
                    <Grid>
                        <FormContainer src={more}>
                            <Row>
                                <Col smOffset={6} sm={5}>
                                    <CommentForm
                                        legend="We love hearing from you!"
                                        help="Have a suggestion? We're always interested! Sharing your ideas helps us make Dot Confirmed better."
                                    />
                                </Col>
                            </Row>
                        </FormContainer>
                    </Grid>
                </section>
            </div>
        );
    }
}

export default About;



