import React from "react";
import {Button, ControlLabel, Form, FormControl, FormGroup} from "react-bootstrap";

const url = "https://formspree.io/letschat@dotconfirmed.com";

const CommentForm = ({legend, help}) => (
    <Form action={url} method="POST">
        <input type="hidden" name="_subject" value="Dot Confirmed"/>
        <input type="hidden" name="_format" value="plain"/>

        <fieldset>
            <h3 className='form-legend'>{legend}</h3>
            <p className='form-help'>{help}</p>

            <FormGroup controlId="name">
                <ControlLabel srOnly>Name</ControlLabel>
                <FormControl
                    type="text"
                    name="name"
                    placeholder="Name"
                />
            </FormGroup>

            <FormGroup controlId="email">
                <ControlLabel srOnly>Email</ControlLabel>
                <FormControl
                    type="email"
                    name="email"
                    placeholder="Email"
                />
            </FormGroup>

            <FormGroup controlId="message">
                <ControlLabel srOnly>Message</ControlLabel>
                <FormControl
                    componentClass="textarea"
                    name="message"
                    placeholder="Message"
                    style={{resize: 'none'}}
                />
            </FormGroup>

            <Button type="submit" bsStyle="primary" block>Send us a message</Button>
        </fieldset>
    </Form>
);

export default CommentForm;




